








































.cartcontrol {
  font-size: 0;
  .cart-decrease {
    display: inline-block;
    padding: 6px;
    opacity: 1;
    transform: translate3d(0, 0, 0);
    .inner {
      display: inline-block;
      line-height: 24px;
      font-size: 24px;
      color: rgb(0, 160, 220);
      transform: rotate(0);
    }
    &.move-enter-active,
    &.move-leave-active {
      transition: all 0.4s linear;
      .inner {
        transition: all 0.4s linear;
      }
    }
    &.move-enter,
    &.move-leave-to {
      transform: translate3d(24px, 0, 0);
      opacity: 0;
      .inner {
        transform: rotate(180deg);
      }
    }
  }
  .cart-count {
    display: inline-block;
    vertical-align: top;
    width: 12px;
    padding-top: 6px;
    line-height: 24px;
    text-align: center;
    font-size: 10px;
    color: rgba(147, 153, 159, 1);
  }
  .cart-add {
    display: inline-block;
    padding: 6px;
    line-height: 24px;
    font-size: 24px;
    color: rgb(0, 160, 220);
  }
}
