





































































































































































@import '@/design/index.scss';

.goods {
  display: flex;
  position: relative;
  top: 0;
  padding-bottom: 46px;
  width: 100%;
  .menu-wrapper {
    flex: 0 0 80px;
    background-color: #f3f5f7;
    overflow: auto;
    -webkit-overflow-scrolling: touch;
    scroll-behavior: smooth;
    .menu-item {
      display: table;
      height: 54px;
      width: 80px;
      line-height: 14px;
      padding: 0 12px;
      &.current {
        position: relative;
        z-index: 10;
        margin-top: -1px;
        background-color: #fff;
        font-weight: 700;
        .text {
          &:after {
            display: none;
          }
        }
      }
      .text {
        .icon {
          display: inline-block;
          vertical-align: top;
          width: 12px;
          height: 12px;
          margin-right: -2px;
          background-size: 12px;
          background-repeat: no-repeat;
          &.decrease {
            @include bg-image('goods/decrease_3');
          }
          &.discount {
            @include bg-image('goods/discount_3');
          }
          &.guarantee {
            @include bg-image('goods/guarantee_3');
          }
          &.invoice {
            @include bg-image('goods/invoice_3');
          }
          &.special {
            @include bg-image('goods/special_3');
          }
        }
        display: table-cell;
        width: 56px;
        vertical-align: middle;
        font-size: 12px;
        @include border-1px(rgba(7, 17, 27, 0.1));
      }
    }
  }
  .foods-wrapper {
    flex: 1;
    overflow: auto;
    -webkit-overflow-scrolling: touch;
    scroll-behavior: smooth;
    .title {
      position: sticky;
      top: 0;
      z-index: 10;
      padding-left: 14px;
      height: 26px;
      line-height: 26px;
      border-left: 2px solid #d9dde1;
      font-size: 12px;
      color: rgb(147, 153, 159);
      background: #f3f5f7;
    }
    .food-item {
      display: flex;
      margin: 18px;
      padding-bottom: 18px;
      @include border-1px(rgba(7,17,27,0.1));
      &:last-child {
        &:after {
          display: none;
        }
        margin-bottom: 0;
      }
      .icon {
        flex: 0 0 57px;
        margin-right: 10px;
      }
      .content {
        flex: 1;
        .name {
          margin: 2px 0 8px;
          line-height: 14px;
          font-size: 14px;
          color: rgb(7, 17, 27);
        }
        .desc, .extra {
          font-size: 10px;
          color: rgb(147, 153, 159);
        }
        .desc {
          margin-bottom: 8px;
        }
        .extra {
          .count {
            margin-right: 12px;
          }
        }
        .price {
          font-weight: 700;
          line-height: 24px;
          .now {
            margin-right: 8px;
            font-size: 14px;
            color: rgb(240, 20, 20);
          }
          .old {
            text-decoration: line-through;
            font-size: 10px;
            color: rgb(147, 153, 159);
          }
        }
        .cartcontrol-wrapper {
          position: absolute;
          right: 0;
          bottom: 12px;
        }
      }
    }
  }
}
